import { isObjEmpty } from "@utils";
import classNames from "classnames";
import { useState } from "react";
import { useForm } from "react-hook-form";
import { FormattedMessage, injectIntl } from "react-intl";
import { useHistory, useParams } from "react-router-dom";
import { toast } from "react-toastify";
import {
  Button,
  Col,
  Form,
  FormGroup,
  Input,
  Label,
  Media,
  Row,
} from "reactstrap";
import api from "../../../../../../constants/api";
import validateOptions from "../../../../../../constants/validate";
import request from "../../../../../../services/request";
const CountryTab = ({ selected, intl }) => {
  const { register, errors, handleSubmit } = useForm();
  const history = useHistory();
  const [disable, setDisable] = useState(false);
  const CountryOptions = validateOptions.CountryOptions;
  const { id } = useParams();
  const onSubmit = async (values) => {
    if (isObjEmpty(errors)) {
      setDisable(true);
      editCountry({
        id,
        data: values,
      });
    }
  };
  const editCountry = (data) => {
    request
      .send({
        method: api.UPDATE_COUNTRY.method,
        path: api.UPDATE_COUNTRY.path,
        data,
      })
      .then((result) => {
        if (result) {
          const { statusCode, data } = result;
          if (statusCode === 200) {
            toast.success(<FormattedMessage id={"Update successful!"} />);
            history.goBack();
          } else if (statusCode == 400) {
            toast.warn(<FormattedMessage id={"Update failed!"} />);
            setDisable(false);
          }
        }
      })
      .catch((result) => {
        setDisable(false);
      });
  };

  return (
    <Row>
      <Col sm="12">
        <Media className="mb-2">
          <Media className="mt-50" body>
            <h4>{selected.username} </h4>
          </Media>
        </Media>
      </Col>
      <Col sm="12">
        <Form onSubmit={handleSubmit(onSubmit)}>
          <FormGroup>
            <Label for="name">
              <FormattedMessage id="name" />{" "}
              <span className="text-danger">*</span>
            </Label>
            <Input
              name="name"
              id="name"
              placeholder=" "
              innerRef={register(CountryOptions.name)}
              onBlur={() => {
                let name = document.getElementById("name");
                if (name && name.value) {
                  name.value = name.value.trim();
                }
              }}
              defaultValue={selected?.name}
              className={classNames({ "is-invalid": errors["name"] })}
            />
            <small className="text-danger">
              {errors?.name && errors.name.message}
            </small>
            {errors?.name?.type == "validate" && (
              <small className="text-danger">
                <FormattedMessage id="Invalid country name" />
              </small>
            )}
          </FormGroup>
          <FormGroup>
            <Label for="code">
              <FormattedMessage id="country code" />{" "}
              <span className="text-danger">*</span>
            </Label>
            <Input
              name="code"
              id="code"
              placeholder=" "
              innerRef={register(CountryOptions.code)}
              onBlur={() => {
                let code = document.getElementById("code");
                if (code && code.value) {
                  code.value = code.value.trim();
                }
              }}
              defaultValue={selected?.code}
              className={classNames({ "is-invalid": errors["code"] })}
            />
            <small className="text-danger">
              {errors?.code && errors.code.message}
            </small>
            {errors?.code?.type == "validate" && (
              <small className="text-danger">
                <FormattedMessage id="Invalid country code" />
              </small>
            )}
          </FormGroup>
          <FormGroup>
            <Label for="phone_code">
              <FormattedMessage id="phone code" />{" "}
              <span className="text-danger">*</span>
            </Label>
            <Input
              name="phone_code"
              id="phone_code"
              placeholder=" "
              innerRef={register(CountryOptions.phone_code)}
              onBlur={() => {
                let phone_code = document.getElementById("phone_code");
                if (phone_code && phone_code.value) {
                  phone_code.value = phone_code.value.trim();
                }
              }}
              defaultValue={selected?.phone_code}
              className={classNames({ "is-invalid": errors["phone_code"] })}
            />
            <small className="text-danger">
              {errors?.phone_code && errors.phone_code.message}
            </small>
            {errors?.phone_code?.type == "validate" && (
              <small className="text-danger">
                <FormattedMessage id="Invalid phone code" />
              </small>
            )}
          </FormGroup>

          <div style={{ textAlign: "end" }}>
            <Button
              type="submit"
              className="mr-1"
              color="primary"
              disabled={disable}
            >
              <FormattedMessage id="update" />
            </Button>
            <Button
              type="reset"
              color="secondary"
              outline
              onClick={() => history.goBack()}
            >
              <FormattedMessage id="Cancel" />
            </Button>
          </div>
        </Form>
      </Col>
    </Row>
  );
};
export default injectIntl(CountryTab);
